.menu {
  box-sizing: border-box;
  height: 100vh;

  background-color: #f9f9f9;
  pointer-events: none;
  font-weight: 700;

  overflow: auto;

  &__top {
    position: sticky;
    top: 0;
    z-index: 1;

    margin-bottom: 32px;

    @include on-tablet {
      margin-bottom: 0;
    }
  }

  &__nav {
    margin-bottom: 48px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;

    font-size: 16px;
    line-height: 140%;
    letter-spacing: 2px;
    color: inherit;
    text-decoration: none;
  }

  &__call-to-order {
    display: block;
    width: fit-content;

    padding-bottom: 7px;
    border-bottom: 1px solid $main-text-color;

    font-size: 16px;
    line-height: 22px;
    letter-spacing: 2px;
    color: inherit;
    text-decoration: none;
    text-transform: uppercase;
  }
}
